 .home-container {
     width: 320px;
     padding: 20px 20px 0 20px;
     margin-left: auto;
     margin-right: auto;
     min-height: calc(100vh - 330px);

     @include tab {
         width: 768px;
         padding: 60px 75px 0 75px;
         min-height: calc(100vh - 307px);
     }

     @include desk {
         width: 1024px;
         padding: 60px 70px 0 70px;
         min-height: calc(100vh - 307px);
     }
 }

 .home-card {
     width: 280px;
     justify-content: center;


     &:last-child {
         margin-right: 0;
     }

     &:not(:last-child) {
         margin-bottom: 20px;
     }

     @include tab {
         width: 294px;
         margin: 0;

         &:not(:nth-last-child(-n+2)) {
             margin-bottom: 30px;
         }

         &:nth-child(odd) {
             margin-right: 30px;
         }
     }

     @include desk {
         width: 273px;
         margin: 0;

         & {
             margin-right: 30px;
         }

         &:nth-last-child(-n+3) {
             margin-bottom: 30px;
         }
         &:nth-of-type(3n){
            margin-right: 0;
         }
     }
 }